.Carrousel {
    text-align: center;
}

.Carrousel-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #282c34;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}


.Carrousel-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
